@import url('https://fonts.googleapis.com/css2?family=Courgette&family=Kalam:wght@300;400;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

:root{
    /** fonts */
    --ft-1: 'Roboto', sans-serif;
    --ft-2: 'Courgette', cursive;
    --ft-3: 'Kalam', cursive;
    --ft-4: 'Poppins', sans-serif;

    /** theme */
    --primary: #6759e9;
    /*--secundary: #24d1e1;*/
    --secundary: #fd4e5e;

    /*** cherry **/
    /*--primary: #7f00ff;
    --secundary: #bb00ff;
    */
    
    /** colors */
    --bg-200: #050210;
    --bg-100: #1f2334;
    --text-200: #4a4a4a;
    --text-100: #667682;
    --border: #4a4a4a;
    --light: #ffffff;
    --green: #74e25b;
    --red: #d90000;

    /** social color*/
    --facebook: #3b5995;
    --twitter: #1da1f2;
    --google: #dd4b39;
    --youtube: #c4302b;
    --vimeo: #1ab7ea;
    --dribbble: #ea4c89;
    --github: #333;
    --instagram: #c13584;
    --pinterest: #e60023;
    --flickr: #0063dc;
    --bitbucket: #205081;
    --linkedin: #0e76a8;
    --whatsapp: #12af0a;
    --tumblr: #35465c;
    --tiktok: #222;
    --discord: #5865f2;
    --twitch: #9146ff;
    
    /** globals */
    font-family: var(--ft-1);
    font-size: 62.5%;

}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: auto;
    min-height: 100vh;
    overflow-x: hidden;
    background: var(--bg-200);
    color: var(--text-200);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body *{
    font-size: 1.6rem;
    font-family: var(--ft-2);
}

:before,
:after{
    content: '';
    position: absolute;
}

a, span{
    display: inline-block;
}

a{
    text-decoration: none;
}

img{
    width: 100%;
    object-fit: cover;
}

ul, ol{
    list-style: none;
}

h1, h2, h3, h4{
    line-height: 120%;
}

button{
    border: none;
    outline: none;
    cursor: pointer;
}

.flex{
    display: flex;
}

.grid{
    display: grid;
}

.col-2{
    grid-template-columns: repeat(2, 1fr);
}

.col-3{
    grid-template-columns: repeat(3, 1fr);
}

.col-4{
    grid-template-columns: repeat(4, 1fr);
}

.container{
    width: 96%;
    max-width: 114rem;
    height: auto;
    position: relative;
    gap: 2.5rem;
    margin: 0 auto;
    z-index: 2;
}

.border{
    position: relative;
    border-radius: .5rem;
    border: solid .1rem var(--border);
}

.border:before,
.section_title:before,
.btn_me:before{
    width: 100%;
    height: 100%;
    background: var(--border);
    border-radius: .5rem;
    top: .5rem;
    left: 0;
    z-index: -1;
}

.section_title{
    height: 3rem;
    border: solid .1rem var(--border);
    border-radius: .4rem;
    align-items: center;
    justify-content: center;
    padding: 0 4rem;
    background: var(--bg-200);
    font-family: var(--ft-1);
    text-transform: uppercase;
    position: relative;
}

.section_title:before{
    border-radius: .4rem;
}

.btn_me{
    width: 6.6rem;
    height: 3rem;
    background: var(--text-100);
    display: flex;
    align-items: center;;
    justify-content: center;
    font-family: var(--ft-1);
    text-transform: uppercase;
    border-radius: .4rem;
    border: solid .1rem var(--border);
    position: relative;
    color: var(--border);
    font-size: 1.2rem;
    transition: .2s ease-in-out;
}

.btn_me:hover{
    color: var(--light);
}

.btn_me:before{
    background: var(--border);
    border-radius: .4rem;
}