.friends_group{
      margin-top: 3rem;
      width: 100%;
}

.friends_group .section_title{
      width: fit-content;
      margin: 0 auto;
}

.friends_list{
      margin-top: 3rem;
}

.friend_row{
      height: 8rem;
      width: 100%;
      border-radius: .4rem;
      position: relative;
      background: var(--bg-200);
      border: solid .1rem var(--border);
      margin-bottom: 1.5rem;
      padding: .4rem;
      gap: 1rem;
}

.friend_row:before{
      width: 100%;
      height: 100%;
      background: var(--border);
      border-radius: .4rem;
      left: 0;
      top: .5rem;
      z-index: -1;
}

.friend_avatar{
      width: 7rem;
      height: 7rem;
      border-radius: .4rem;
      overflow: hidden;
      flex-shrink: 0;
}

.friend_info{
      flex-grow: 1;
      position: relative;
      z-index: 2;
}

.friend_info strong{
      color: var(--text-100);
      padding-top: 1rem;
      display: block;
}


.friend_info .btn_me{
      position: absolute;
      right: 1rem;
      bottom: 1rem;
}