.gallery_group h1{
      width: 30rem;
      margin: 0 auto;
}

.gallery_content{
      width: 100%;
      height: auto;
      margin-top: 3rem;
      margin-bottoM: 10rem;
}

.slider_content{
      width: 64rem;
      height: 35rem;
      position: relative;
      margin: 0 auto;
}



.image_box{
      width: 32rem;
      height: 34.5rem;
      position: relative;
      border-radius: .5rem;
      padding: 0 .5rem;
}

.image_box:before{
      width: calc(100% - 1rem);
      height: 100%;
      background: var(--border);
      border-radius: .5rem;
      z-index: -1;
}

.image_box__item{
      width: 100%;
      height: 33.7rem;
      border-radius: .5rem;
      overflow: hidden; 
      border: solid .1rem var(--border);
}

.btn_zoom{
      width: 4rem;
      height: 4rem;
      position: absolute;
      top: 1.5rem;
      right: 1.5rem;
      align-items: center;
      justify-content: center;
      border-radius: .4rem;
      background: rgba(0,0,0,.6);
}

.btn_zoom *{
      color: var(--light);
      font-size: 3rem;
}

button.slick-arrow{
      width: 4rem;
      height: 4rem;
      background: var(--bg-200);
      border-radius: 50%;
      border: solid .1rem var(--border);
      z-index: 2;
      top: 17rem;
}

button.slick-arrow:hover{
      background: var(--bg-200);
}

button.slick-arrow.slick-next{
      right: -4.4rem;
}

button.slick-arrow.slick-prev{
      left: -4.4rem;
}

button.slick-arrow.slick-next:before,
button.slick-arrow.slick-prev:before{
      width: 1.4rem;
      height: 1.4rem;
      color: transparent;
      transform: rotate(45deg);
      top: 1.2rem;
      left: .8rem;
}

button.slick-arrow.slick-next:before{
      border-top: solid .1rem var(--text-100);
      border-right: solid .1rem var(--text-100);
}


button.slick-arrow.slick-prev:before{
      border-bottom: solid .1rem var(--text-100);
      border-left: solid .1rem var(--text-100);
      left: 1.4rem;
}