.relation_group{
      flex-direction: column;
      align-items: center;
      width: 100%;
}

.relation_avatar{
      width: 100%;
      height: 20.5rem;
      margin-top: 2rem;
      position: relative;
}

.relation_avatar__img{
      width: 100%;
      height: 20rem;
      border-radius: .5rem;
      overflow: hidden;
}

.relation_avatar .btn_me{
      position: absolute;
      right: 2rem;
      bottom: -1.5rem;
}

.relation_info{
      margin-top: 2rem;
}

.relation_info h2{
      font-size: 2.6rem;
      color: var(--text-100);
}

.relation_description{
      margin-top: .5rem;
}