.modal_content{
      width: 100%;
      height: 100vh;
      position: fixed;
      top: 0;
      left: 0;
      background: rgba(0,0,0,.7);
      z-index: 50;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: .5s ease-in-out;
      opacity: 0;
      visibility: hidden;
}

.modal_content.active{
      opacity: 1;
      visibility: visible;
}

.modal_inner{
      width: fit-content;
      max-width: 80%;
      height: fit-content;
      max-height: 80vh;
      border-radius: 1rem;
      overflow: hidden;
}

.close_modal{
      width: 4rem;
      height: 4rem;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: .4rem;
      position: absolute;
      bottom: 2rem;
      right: 2rem;
      background: var(--text-100);
}

.close_modal *{
      color: var(--light);
}