
.header_top{
      width: 100%;
      height: auto;
      gap: 2rem;
      padding-top: 9rem;
}

.user_group,
.user_description{
      width: 50%;
}

.user_group{
      gap: 1rem;
}

.user_avatar{
      width: 16rem;
      height: 17.7rem;
      position: relative;
}

.user_avatar__img{
      width: 100%;
      height: 17.2rem;
      border-radius: .5rem;
      overflow: hidden;
      background: var(--bg-200);
}

.user_info{
      flex-direction: column;
      padding-top: 4rem;
}

.user_info h1{
      color: var(--text-100);
      text-transform: capitalize;
      font-size: 2.8rem;
}

.user_info strong{
      font-family: var(--ft-1);
      margin-top: .5rem;
      font-size: 2rem;
}

.user_location{
      gap: .5rem;
      margin-top: 1rem;
      align-items: flex-start;
}

.user_location img{
      width: 3.5rem;
}

.user_location span{
      font-weight: 600;
      font-size: 2.4rem;
}