.videos_group .section_title{
      width: 30rem;
      margin: 0 auto;
}

.videos_content{
      margin-top: 3rem;
      flex-direction: column;
      align-items: center;
}

.video_group{
      width: 64rem;
      height: auto;
      flex-direction: column;
      margin-bottom: 3rem;
}

.video_frame{
      width: 64rem;
      height: 36rem;
      border-radius: 1rem;
      overflow: hidden;
}

.video_frame iframe{
      border: none;
      outline: none;
}

.video_group p{
      margin-top: 1.5rem;
      position: relative;
      padding-left: 2rem;
}

.video_group p:before{
      width: 1rem;
      height: 1rem;
      background: var(--text-100);
      border-radius: 50%;
      left: 0;
      top: .5rem;
}