.user_online{
      gap: .5rem;
      align-items: center;
}

.dot_online{
      width: 1rem;
      height: 1rem;
      border-radius: 50%;
}

.dot_online.red{
      background: var(--red);
}

.dot_online.green{
      background: var(--green);
}

.user_online span{
      font-family: var(--ft-1);
      text-transform: uppercase;
      font-size: 1rem;
      font-weight: 500;
}